import { Directive,Input, HostListener,ElementRef } from '@angular/core';

import {MdGestureConfig} from '../../core/gestures/MdGestureConfig';
import {HAMMER_GESTURE_CONFIG} from '@angular/platform-browser';

@Directive({
    selector:"[swipe-count]"
})
export class XxjSwipeCountDirective{
    constructor(private _el:ElementRef){}
    swipeNumber:number=0;
    text:string="";
    @HostListener('swipe',['$event.target'])
    onSwipe(el:Element){
        this.swipeNumber++;
        let str=this._el.nativeElement.textContent;
        str=str+'<br> you swipe '+this.swipeNumber;
        this._el.nativeElement.textContent=str;
    }
}